import React ,{Component} from 'react';
import RouterMap from '../routes/index'
// app
export default class App extends Component {
    constructor(){
        super();
        this.state = {
            done:false
        }
    }
    render(){
        return (
            <div>
                {
                    this.state.done?<RouterMap />:<div></div>
                }
            </div>
        )
    }
    componentDidMount(){
        this.setState({
            done:true
        });
    }
}


